.Header {
    position: fixed !important;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, 0.0) !important;
    box-shadow: 0px 0px 0px 0px, 0px 0px 0px 0px, 0px 0px 0px 0px !important;
    z-index: 1;
    transition: box-shadow 10ms, background 300ms !important;
}

.Footer {
    background-color: #222222 !important;
    color: white !important;
    position: relative;
    display: flex;
    justify-content: center;
}